import Input from '../elements/input';
import '../media/App.css';
import axios from 'axios';
import { useState } from 'react';
import { navigate } from "@reach/router"

const baseUrl='https://backends.smartrater.us/'

const Login =(props)=> {
    const [email, setEmail] = useState('');
    const [password, setPassword] = useState('');
    const [error, setError] = useState('');
    const [loading, setLoading] = useState(false);
    
    const handleLoading = (loading) => {
        setLoading(loading);
        let neoElements ={...props.userPack.elements}
        neoElements.isLoading = loading;
        props.userPack.methods.stateHandler('elements',neoElements);
    }
    const login = () =>{
        let username = email
        handleLoading(true);
        console.log("qlokentucky",username,password)
        axios.post(baseUrl+"token-auth/",{username,password})
        
        .then(res=>{
          console.log("success")
          saveToken(res.data.user,res.data.token)
          //set loggedIn to true
          let elementos ={...props.userPack.elements}
            elementos.loggedIn = true;
          props.userPack.methods.stateHandler('elements',elementos);
          handleLoading(false);
          navigate('/',{replace:true})
            return
        },
        error=>{
            console.log("error")
          try {
            setError(error.response.data.detail? 
                error.response.data.detail : "Usuario y/o contrasea incorrecta")
                 
          } catch (error) {
            setError("Usuario y/o contrasea incorrecta")
                   
          }

            handleLoading(false);
            return false
      })}

    const saveToken=(user,token)=>{
        window.localStorage.setItem('token',token);
        window.localStorage.setItem('userId',user.id);
        props.userPack.methods.stateHandler('user',user);
        props.userPack.methods.stateHandler('token',token);
        //let notifications = [].concat(user.activeLeasing).concat(user.activeRenting)
        //const notificationClient = this.openNotifications(user.id)
    }
    const handleSubmit = (e) => {
        e.preventDefault();
        axios.post('http://localhost:5000/api/auth/login', {
            email: email,
            password: password
        })
            .then(res => {
                console.log(res);
                console.log(res.data);
                localStorage.setItem('token', res.data.token);
                props.history.push('/');
            }
            ).catch(err => {
                console.log(err);});
    }

  return (
<div className="Login" style={{width: 277, paddingLeft: 17, paddingRight: 17, paddingTop: 20, paddingBottom: 20, backgroundColor: 'rgba(252.88, 252.88, 252.88, 1)', display: 'inline-flex', flexDirection: 'column', alignItems: 'center', justifyContent: 'center',}}>
    <p className="SmartQuotes" style={{width: '100%', fontSize: 24, fontWeight: '700', lineHeight: '100%', textAlign: 'center', color: 'rgba(62, 62, 62, 1)',}}>Smart Quotes</p>
    <div style={{height: 20,}}/>
    <div className="modalContent" style={{width: '100%', height: 194, display: 'flex', flexDirection: 'column', alignItems: 'center', justifyContent: 'center',}}>
        {
            loading?
            <></>
            :
            <>
             <div className="fieldBox" style={{width: '100%', display: 'flex', flexDirection: 'column', alignItems: 'flex-start', justifyContent: 'flex-start',}}>
            <p className="AccountName" style={{fontSize: 12, fontWeight: '700', lineHeight: '100%', color: 'black',}}>User</p>
            <Input placeholder="Username"
            value={email}
            method={setEmail}/>
        </div>
        <div className="fieldBox" style={{marginTop:10,width: '100%', display: 'flex', flexDirection: 'column', alignItems: 'flex-start', justifyContent: 'flex-start',}}>
            <p className="Password" style={{fontSize: 12, fontWeight: '700', lineHeight: '100%', color: 'black',}}>Password</p>
            <Input type="password" 
            value={password}
            placeholder="Password" method={setPassword} />
        </div>
            </>
        }
       
       
    </div>
    <div className="Frame3 link" onClick={login}style={{width: '100%', height: 28, backgroundColor: 'rgba(101.44, 213.56, 159.74, 1)', borderRadius: 20, display: 'flex', flexDirection: 'column', alignItems: 'center', justifyContent: 'center',}}>
        <p className="Login" style={{fontSize: 12, fontWeight: '700', lineHeight: '100%', textAlign: 'center', color: 'white',}}>Login</p>
    </div>
</div>
  );
}

export default Login;
